.homeWrap {
    .article2 {
        .row {
            .local {
                display:flex;
                flex-wrap:wrap;
                padding:30px 0;
                li {
                    width:33.33%; padding:20px; 
                    a { display:flex; align-items:center;
                        img { width:50%; margin-right:10px; border-radius:10px }
                    }
                }
            }
            .lifeStyle {
                h2 { font-size:20px }
                ul {
                    display:flex;
                    li { 
                        flex:1; padding:20px 20px 20px 0;
                        &:last-child { padding-right:0px }
                    }
                }
            }
        }
    }

    .article3 { background:#000; padding:100px 0; color:#fff;
        .row { display:flex; justify-content:space-between; 
            div.rowchild { width:52%;
                img { border-radius:40px 40px 0px 0px; }
                p { background:#999; 
                    border-radius:0px 0px 40px 40px;
                    padding:20px; 
                }
                &.right { width:46%;
                    display:flex; flex-direction:column;
                    justify-content:space-between;
                    .rightRow1 { display:flex; justify-content:space-between;
                        div { width:48% }
                    }
                }
            }
        }
    }
}